@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900');

/* #FFF8F2 */

* {
  margin: 0;
  padding: 0;
}

body {
  /* background: #f0f4f3; */
  background: #FFFFFF; 
  font-family: 'Roboto', sans-serif;
}

.button {
  border: 1px solid #fff;
  border-radius: 20px;
  color: #fff;
  font-size: 11px;
  font-weight: 500;
  letter-spacing: 1px;
  padding: 15px 60px;
  text-decoration: none;
  text-transform: uppercase;
}

input[type="email"],
input[type="password"],
input[type="text"] {
  background: #f4f8f7;
  border: none;
  font-weight: 300;
  margin: 5px;
  padding: 10px;
  width: 280px;
}

.submit-btn {
  border: none;
  border-radius: 25px;
  color: #fff;
  cursor: pointer;
  font-size: 11px;
  letter-spacing: 1px;
  margin-top: 20px;
  padding: 15px 50px;
  text-transform: uppercase;
}

.submit-btn, .spcial{
margin-top: 4px;
}

#login .submit-btn {
  background: #eeac73;
}

#register .submit-btn {
  background: #eeac73;
}

#container {
  /* background: #ededed; */
  background: #fff;
  border-radius: 10px;
  border-color: #eeac73;
  height: 460px;
  margin:  auto;
  overflow: hidden;
  width: 1000px;
  margin-top: 3vh;
}

.social-login {
  border: 1px solid #ccc;
  border-radius: 50px;
  height: 15px;
  padding: 10px;
  width: 15px;
}

#login {
  background: #fff;
  float: left;
  height: 100%;
  position: relative;
  width: 50%;
  text-align: center;
  top: -550px;
  z-index: 1;
}

#register {
  background: #fff;
  float: right;
  height: 110%;
  position: relative;
  width: 50%;
  text-align: center;
  top: -620px;
  z-index: 1;
}

#login h1,
#register h1 {
  padding: 20% 0 25px;
}

#login h1 {
  /* color: #3aa7b1; */
  color: #eb9143;
}

#register h1 {
  /* color: #3aaf9f; */
  color: #eb9143;
}

#login p,
#register p {
  font-size: 12px;
  font-weight: 300;
  letter-spacing: 0.3px;
  padding: 20px;
}

#forgot-pass {
  border-bottom: 1px solid #ccc;
  color: #000;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0.3px;
  padding: 3px;
  text-decoration: none;
}

#forgot-pass:hover{
  cursor: pointer;
}

#cover {
  /* background: #3aa7b1; */
  background: #eeac73;
  /* background: #fff; */
  color: #fff;
  height: 550px;
  margin: 0 0 0 50%;
  position: relative;
  text-align: center;
  transition: margin 0.7s, background 0.5s, display 1s, width 1s;
  width: 50%;
  z-index: 5;
}

#cover h1 {
  padding-top: 25%;
}

#cover p {
  font-weight: 300;
  line-height: 22px;
  padding: 30px 45px 40px;
}

.sign-in {
  display: none;
}

.sub {
  position: relative;
  top: -11px;
}

#cover:target {
  background: #eeac73;
  margin: 0;
}

#cover:target .sign-up {
  display: none; 
}

#cover:target .sign-in {
  display: inline-block; 
}

#cover:target .login-div {
  width: 35%;
}

#credit {
  color: #999;
  font-size: 14px;
}

#info {
  background: #eaeaea;
  color: #555;
  padding: 20px;
  text-align: center;
}